/* === Resume Section === */

/*---------------------------------------------------------
Resume Timeline
-----------------------------------------------------------*/
.resume-title h3{
  text-align: center;
}
.resume-section > div > div{
  margin-bottom: 60px;
}
.resume-section > div > div:nth-of-type(2){
  margin-bottom: 0;
}
.resume-title{
  margin-bottom: 30px;
}
.timeline {
  position: relative;
  padding: 0;
  list-style: none;
}

.timeline-body {
  text-align: left !important;
}

.resume{
  position: relative;
}

.resume:before{
  content: "";
  position: absolute;
  top: -8px;
  width: 10px;
  height: 10px;
  background-color: #e1e1e1;
  left: 50%;
  margin-left: -6px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.resume:after{
  content: "";
  position: absolute;
  bottom: -8px;
  width: 10px;
  height: 10px;
  background-color: #e1e1e1;
  left: 50%;
  margin-left: -6px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.timeline:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 2px;
  margin-left: -1.5px;
  background-color: #e1e1e1;
}

.timeline>li {
  position: relative;
  margin-bottom: 50px;
  min-height: 50px;
}

.timeline>li:before,
.timeline>li:after {
  content: " ";
  display: table;
}

.timeline>li:after {
  clear: both;
}

.timeline>li .timeline-panel {
  float: left;
  position: relative;
  width: 100%;
}

.timeline-content{
  padding:5px 20px 0 20px;
}

.timeline>li .timeline-panel:before {
  right: auto;
  left: -15px;
  border-right-width: 15px;
  border-left-width: 0;
}

.timeline>li .timeline-panel:after {
  right: auto;
  left: -14px;
  border-right-width: 14px;
  border-left-width: 0;
}

.timeline>li .posted-date {
  position: relative;
  text-align: center;
  background-color: #fff;
  left: 20px;
}


.timeline>li .posted-date .month {
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
  display: block;
  color: #2c3e50;
  top: 0;
  float: left;
}

.timeline>li.timeline-inverted>.timeline-panel {
  float: right;
  text-align: left;
}

.timeline>li.timeline-inverted>.timeline-panel:before {
  right: auto;
  left: -15px;
  border-right-width: 15px;
  border-left-width: 0;
}

.timeline>li.timeline-inverted>.timeline-panel:after {
  right: auto;
  left: -14px;
  border-right-width: 14px;
  border-left-width: 0;
}

.timeline>li:last-child {
  margin-bottom: 0;
}

.timeline-heading h3 {
  font-size: 20px;
  line-height: 28px;
}
.timeline-heading span{
  display: block;
  font-weight: 700;
  margin-bottom: 20px;
}

@media(min-width:992px) {
  .timeline:before {
    left: 50%;
  }

  .timeline>li {
    margin-bottom: 15px;
    min-height: 100px;
  }

  .timeline>li .posted-date {
    position: absolute;
    text-align: center;
    background-color: #fff;
  }

  .timeline>li .timeline-panel {
    float: left;
    width: 43%;
    text-align: right;
    border: 1px solid #eee;
    position: relative;
  }

  .timeline-content{
    position: relative;
    padding: 24px;
    background-color: #fff;
    -webkit-transition:all 400ms;
    -moz-transition:all 400ms;
    -o-transition:all 400ms;
    transition: all 400ms;
  }

  .timeline-content::after{
    border: 1px solid #eee;
    width: 100%;
    position: absolute;
    content: "";
    bottom: -3px;
    height:10px;
    left: 0;
    z-index: -1;
  }

  .timeline>li .timeline-content:hover {
    background: #68c3a3;
    color: #fff;
  }

  .timeline>li .timeline-panel::before,
  .timeline>li.timeline-inverted .timeline-panel::before {
    content: "";
    top: 25px;
    position: absolute;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
  }
  .timeline>li .timeline-panel::before {
    left: 100%;
    border-left: 12px solid #ECECEC;
    -webkit-transition:all 400ms;
    -moz-transition:all 400ms;
    -o-transition:all 400ms;
    transition: all 400ms;
  }
  .timeline>li .timeline-panel:hover::before {
    border-left: 12px solid #68c3a3;
  }

  .timeline>li.timeline-inverted .timeline-panel::before {
    right: 100%;
    border-right: 12px solid #ECECEC;
    -webkit-transition:all 400ms;
    -moz-transition:all 400ms;
    -o-transition:all 400ms;
    transition: all 400ms;
  }
  .timeline>li.timeline-inverted .timeline-panel:hover::before {
    border-right: 12px solid #68c3a3;
    border-left: 0;
  }

  .timeline>li .timeline-panel::after,
  .timeline>li.timeline-inverted .timeline-panel::after {
    content: "";
    top: 25px;
    position: absolute;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
  }
  .timeline>li .timeline-panel::after {
    left: 100%;
    margin-left: -2px;
    border-left: 12px solid #FFFFFF;
    -webkit-transition:all 400ms;
    -moz-transition:all 400ms;
    -o-transition:all 400ms;
    transition: all 400ms;
  }
  .timeline>li .timeline-panel:hover::after {
    border-left: 12px solid #68c3a3;
  }

  .timeline>li.timeline-inverted .timeline-panel::after {
    right: 100%;
    margin-right:-2px;
    border-right: 12px solid #FFFFFF;
    -webkit-transition:all 400ms;
    -moz-transition:all 400ms;
    -o-transition:all 400ms;
    transition: all 400ms;
  }
  .timeline>li.timeline-inverted .timeline-panel:hover::after {
    border-right: 12px solid #68c3a3;
    border-left: 0;
  }

  .timeline>li .posted-date {
    left: 50%;
    width: 120px;
    margin-left: -60px;
    margin-top: 28px;
  }
  .timeline>li .posted-date .month {
    float: none;
  }


}

/* === Skill Section === */
.skills-section {
  background-color: #1a1e23;
  color: #fff;
}
.skills-section .section-title{
  color: #fff;
  margin-bottom: 110px;
}
.skills-section .section-title h2{
  color: #fff;
}
.skill-title {
  width: 25%;
  float: left;
}
.skill-title h3{
  font-size: 18px;
  font-weight: 400;
  margin-top: -6px;
  text-transform: capitalize;
}

/* progress-bar */

.skill-progress {
  position: relative;
}
.progress {
  height: 10px;
  margin-bottom: 40px;
  background-color: #3a4149;
  border-radius: 3px;
  box-shadow: none;
  width: 75%;
}

.progress-bar {
  background-color: #68c3a3;
}
.progress-bar span{
  position: absolute;
  top: -20px;
  right: 15px;
}

.progress .progress-bar.six-sec-ease-in-out {
  -webkit-transition: width 2s ease-in-out;
  -moz-transition: width 2s ease-in-out;
  -o-transition: width 2s ease-in-out;
  transition:  width 2s ease-in-out;
}


.skill-chart h3 {
  margin-top: 20px;
}

.our-progress{
  margin:10px 0 30px;
}

.progress-percent{
  margin-left: auto;
  margin-right: auto;
}

.our-progress h3{
  font-weight: 400;
  margin-bottom: 5px;
}

/* === Chart CSS  === */
.chart {
  position: relative;
  display: inline-block;
  width: 140px;
  height: 140px;
  margin: 40px auto;
  text-align: center;
}
.chart canvas {
  position:absolute;
  top:0;
  left:0;
}
.percent {
  display: inline-block;
  margin: auto;
  line-height: 140px;
  z-index: 2;
  font-size: 25px;
  font-weight: 500;
  text-align: center;
}

.percent:after {
  content: '%';
}

.chart-text span {
  font-size: 18px;
  font-weight: 400;
  text-transform: capitalize;
  display: block;
  margin-top: 20px;
}

/*.percent:after {content: '%';margin-left: 0.1em;font-size: .8em;}*/
.angular{margin-top:100px;}
.angular .chart {margin-top:0;}
.chart .chart-text h3{font-size:22px;}



/* === Work Section === */
.works-section {
  background-color: #f7f7f7;
}
.portfolio-item {
  padding-right: 5px;
  padding-left: 5px;
  min-height: 235px;
}
.portfolio-bg{
  background: #fff;
  padding: 5px;
  margin-bottom: 20px;
  border: 1px solid #eee;
  border-bottom: 3px double #eee;
}


.portfolio {
  position: relative;
  overflow: hidden;
}


/* Shuffle Filter*/
#filter {
  margin: 0 0 35px;
  text-align: center;
}


#filter li a {
  color: #68c3a3;
  display: block;
  text-transform: uppercase;
  padding: 0 15px;
  border-radius: 4px;
  transition: inherit;
}
#filter li a.active,
#filter li a:hover {
  position: relative;
  background: #68c3a3;
  color: #fff;
  border-bottom: 3px solid rgba(0, 0, 0, 0.15);
}

#filter li a.active::after,
#filter li a:hover::after{
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 10px solid rgba(104, 195, 163, 1);
  bottom: -9px;
  left: 50%;
  margin-left: -8px;
}

.tt-overlay {
  position: absolute;
  background-color: rgba(26, 30, 31, 0.85);
  width: 100%;
  height: 100%;
  opacity: 0;
}

.portfolio-info,
.links,
.links a i,
.links a i::after,
.tt-overlay {
  -webkit-transition: all 400ms;
  -moz-transition: all 400ms;
  -o-transition: all 400ms;
  transition: all 400ms;
}


.links {
  width: 90px;
  height: 40px;
  position: absolute;
  top: 40%;
  left: 50%;
  margin-top: -20px;
  margin-left: -45px;
  opacity: 0;
  -webkit-transform: translate(0,-50px);
  -moz-transform: translate(0,-50px);
  -ms-transform: translate(0,-50px);
  -o-transform: translate(0,-50px);
  transform: translate(0,-50px);
}

.links a i{
  position: relative;
  color: #68c3a3;
  font-size: 16px;
  margin: 0 13px;
  z-index: 100;
}
.links a:hover i {
  color:  #fff;
}
.links a i::after{
  position: absolute;
  content: "";
  left: -7px;
  top: -7px;
  width: 30px;
  height: 30px;
  border: 1px solid #68c3a3;
  z-index: -1;
  border-radius: 3px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
.links a:hover i::after {
  background-color: #68c3a3;
}

.portfolio-info {
  position: absolute;
  bottom: -100%;
  left: 0;
  background-color: #68c3a3;
  color: #fff;
  height: 45px;
  width: 100%;
  text-align: center;
}

.portfolio:hover .tt-overlay,
.portfolio:hover .links {
  opacity: 1;
  -webkit-transform: translate(0,0);
  -moz-transform: translate(0,0);
  -ms-transform: translate(0,0);
  -o-transform: translate(0,0);
  transform: translate(0,0);
}
.portfolio:hover .portfolio-info{
  bottom: 0;
}
.portfolio-info h3 {
  font-size: 14px;
  line-height: 45px;
  font-weight: 700;
  margin: 0;
}


/*PopUp fade effect*/
.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
  opacity: 0;
  -webkit-backface-visibility: hidden;
  /* ideally, transition speed should match zoom duration */
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.mfp-with-zoom.mfp-ready .mfp-container {
  opacity: 1;
}
.mfp-with-zoom.mfp-ready.mfp-bg {
  opacity: 0.8;
}

.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
  opacity: 0;
}

