}


/*SERVICE SECTION*/
.service-section {
  background: url(../images/service-bg.jpg) no-repeat center center;
  background-size: cover;
  padding:120px 0;
}
.service-section h2 {
  color: #fff;
}
.service-wrap{
  padding: 40px 0
}
.service-box {
  text-align: center;
  position: relative;
  width: 180px;
  height: 180px;
  margin: 0 auto;
}
.service-box::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  border: 1px solid #fff;
  width: 180px;
  height: 180px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  border-radius: 15px;
}
.service-box i {
  font-size: 40px;
  color: #fff;
  line-height: 100px;
  z-index: 100;
  position: relative;
}
.service-box h3 {
  font-size: 18px;
  line-height: 18px;
  color: #fff;
  z-index: 100;
  position: relative;
}
.service-box:hover.service-box::after{
  background: #fff;
  border: 1px solid transparent;
}
.service-box:hover i,
.service-box:hover h3{
  color: #2c3e50;
}
.service-box i,
.service-box h3,
.service-box::after {
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
}

